.navbar
  background-color: var(--navbar-background-color)
  min-height: var(--navbar-height)
  position: relative
  z-index: var(--navbar-z)
  @each $name, $pair in $navbar-colors
    $color: list.nth($pair, 1)
    $color-invert: list.nth($pair, 2)
    &.is-#{$name}
      --navbar-burger-color: var(--#{$name}-invert)
      background-color: var(--#{$name})
      color: var(--#{$name}-invert)
      .navbar-brand
        & > .navbar-item,
        .navbar-link
          color: var(--#{$name}-invert)
        & > a.navbar-item,
        .navbar-link
          &:focus,
          &:hover,
          &.is-active
            --navbar-item-hover-background-color: #{utilities.bulmaVarDarken($name, 5%)}
            background-color: var(--navbar-item-hover-background-color)
            color: var(--#{$name}-invert)
        .navbar-link
          &::after
            border-color: var(--#{$name}-invert)
      +utilities.from($navbar-breakpoint)
        --navbar-dropdown-item-active-background-color: var(--#{$name})
        --navbar-dropdown-item-active-color: var(--#{$name}-invert)
        .navbar-brand,
        .navbar-start,
        .navbar-end
          & > .navbar-item,
          .navbar-link
            color: var(--#{$name}-invert)
          & > a.navbar-item,
          .navbar-link
            &:focus,
            &:hover,
            &.is-active
              --navbar-item-hover-background-color: #{utilities.bulmaVarDarken($name, 5%)}
              background-color: var(--navbar-item-hover-background-color)
              color: var(--#{$name}-invert)
          .navbar-link
            &::after
              border-color: var(--#{$name}-invert)
        .navbar-item.has-dropdown:focus .navbar-link,
        .navbar-item.has-dropdown:hover .navbar-link,
        .navbar-item.has-dropdown.is-active .navbar-link
          --navbar-dropdown-item-hover-background-color: #{utilities.bulmaVarDarken($name, 5%)}
          background-color: var(--navbar-dropdown-item-hover-background-color)
          color: var(--#{$name}-invert)
        .navbar-dropdown
          a.navbar-item
            &.is-active
              background-color: var(--#{$name})
              color: var(--#{$name}-invert)
  & > .container
    align-items: stretch
    display: flex
    min-height: var(--navbar-height)
    width: 100%
  &.has-shadow
    box-shadow: var(--navbar-box-shadow-size) var(--navbar-box-shadow-color)
  &.is-fixed-bottom,
  &.is-fixed-top
    +navbar-fixed
  &.is-fixed-bottom
    bottom: 0
    &.has-shadow
      box-shadow: var(--navbar-bottom-box-shadow-size) var(--navbar-box-shadow-color)
  &.is-fixed-top
    top: 0

html,
body
  &.has-navbar-fixed-top
    padding-top: var(--navbar-height)
  &.has-navbar-fixed-bottom
    padding-bottom: var(--navbar-height)

.navbar-brand,
.navbar-tabs
  align-items: stretch
  display: flex
  flex-shrink: 0
  min-height: var(--navbar-height)

.navbar-brand
  a.navbar-item
    &:focus,
    &:hover
      background-color: transparent

.navbar-tabs
  +utilities.overflow-touch
  max-width: 100vw
  overflow-x: auto
  overflow-y: hidden

.navbar-burger
  @extend %reset
  color: var(--navbar-burger-color)
  +utilities.hamburger(var(--navbar-height))
  +utilities.ltr-property("margin", auto, false)

.navbar-menu
  display: none

.navbar-item,
.navbar-link
  color: var(--navbar-item-color)
  display: block
  line-height: 1.5
  padding: 0.5rem 0.75rem
  position: relative
  .icon
    &:only-child
      margin-left: -0.25rem
      margin-right: -0.25rem

a.navbar-item,
.navbar-link
  cursor: pointer
  &:focus,
  &:focus-within,
  &:hover,
  &.is-active
    background-color: var(--navbar-item-hover-background-color)
    color: var(--navbar-item-hover-color)

.navbar-item
  flex-grow: 0
  flex-shrink: 0
  img
    max-height: var(--navbar-item-img-max-height)
  &.has-dropdown
    padding: 0
  &.is-expanded
    flex-grow: 1
    flex-shrink: 1
  &.is-tab
    border-bottom: 1px solid transparent
    min-height: var(--navbar-height)
    padding-bottom: calc(0.5rem - 1px)
    &:focus,
    &:hover
      background-color: var(--navbar-tab-hover-background-color)
      border-bottom-color: var(--navbar-tab-hover-border-bottom-color)
    &.is-active
      background-color: var(--navbar-tab-active-background-color)
      border-bottom-color: var(--navbar-tab-active-border-bottom-color)
      border-bottom-style: var(--navbar-tab-active-border-bottom-style)
      border-bottom-width: var(--navbar-tab-active-border-bottom-width)
      color: var(--navbar-tab-active-color)
      padding-bottom: calc(0.5rem - var(--navbar-tab-active-border-bottom-width))

.navbar-content
  flex-grow: 1
  flex-shrink: 1

.navbar-link:not(.is-arrowless)
  +utilities.ltr-property("padding", 2.5em)
  &::after
    @extend %arrow
    border-color: var(--navbar-dropdown-arrow)
    margin-top: -0.375em
    +utilities.ltr-position(1.125em)

.navbar-dropdown
  font-size: 0.875rem
  padding-bottom: 0.5rem
  padding-top: 0.5rem
  .navbar-item
    padding-left: 1.5rem
    padding-right: 1.5rem

.navbar-divider
  background-color: var(--navbar-divider-background-color)
  border: none
  display: none
  height: var(--navbar-divider-height)
  margin: 0.5rem 0

+utilities.until($navbar-breakpoint)
  .navbar > .container
    display: block
  .navbar-brand,
  .navbar-tabs
    .navbar-item
      align-items: center
      display: flex
  .navbar-link
    &::after
      display: none
  .navbar-menu
    background-color: var(--navbar-background-color)
    box-shadow: 0 8px 16px var(--navbar-shadow-color)
    padding: 0.5rem 0
    &.is-active
      display: block
  // Fixed navbar
  .navbar
    &.is-fixed-bottom-touch,
    &.is-fixed-top-touch
      +navbar-fixed
    &.is-fixed-bottom-touch
      bottom: 0
      &.has-shadow
        box-shadow: 0 -2px 3px var(--navbar-shadow-color)
    &.is-fixed-top-touch
      top: 0
    &.is-fixed-top,
    &.is-fixed-top-touch
      .navbar-menu
        +utilities.overflow-touch
        max-height: calc(100vh - var(--navbar-height))
        overflow: auto
  html,
  body
    &.has-navbar-fixed-top-touch
      padding-top: var(--navbar-height)
    &.has-navbar-fixed-bottom-touch
      padding-bottom: var(--navbar-height)

+utilities.from($navbar-breakpoint)
  .navbar,
  .navbar-menu,
  .navbar-start,
  .navbar-end
    align-items: stretch
    display: flex
  .navbar
    min-height: var(--navbar-height)
    &.is-spaced
      padding: var(--navbar-padding-vertical) var(--navbar-padding-horizontal)
      .navbar-start,
      .navbar-end
        align-items: center
      a.navbar-item,
      .navbar-link
        border-radius: var(--radius)
    &.is-transparent
      a.navbar-item,
      .navbar-link
        &:focus,
        &:hover,
        &.is-active
          background-color: transparent !important
      .navbar-item.has-dropdown
        &.is-active,
        &.is-hoverable:focus,
        &.is-hoverable:focus-within,
        &.is-hoverable:hover
          .navbar-link
            background-color: transparent !important
      .navbar-dropdown
        a.navbar-item
          &:focus,
          &:hover
            background-color: var(--navbar-dropdown-item-hover-background-color)
            color: var(--navbar-dropdown-item-hover-color)
          &.is-active
            background-color: var(--navbar-dropdown-item-active-background-color)
            color: var(--navbar-dropdown-item-active-color)
  .navbar-burger
    display: none
  .navbar-item,
  .navbar-link
    align-items: center
    display: flex
  .navbar-item
    &.has-dropdown
      align-items: stretch
    &.has-dropdown-up
      .navbar-link::after
        transform: rotate(135deg) translate(0.25em, -0.25em)
      .navbar-dropdown
        border-bottom: var(--navbar-dropdown-border-top)
        border-radius: var(--navbar-dropdown-radius) var(--navbar-dropdown-radius) 0 0
        border-top: none
        bottom: 100%
        box-shadow: 0 -8px 8px var(--navbar-shadow-color)
        top: auto
    &.is-active,
    &.is-hoverable:focus,
    &.is-hoverable:focus-within,
    &.is-hoverable:hover
      .navbar-dropdown
        display: block
        .navbar.is-spaced &,
        &.is-boxed
          opacity: 1
          pointer-events: auto
          transform: translateY(0)
  .navbar-menu
    flex-grow: 1
    flex-shrink: 0
  .navbar-start
    justify-content: flex-start
    +utilities.ltr-property("margin", auto)
  .navbar-end
    justify-content: flex-end
    +utilities.ltr-property("margin", auto, false)
  .navbar-dropdown
    background-color: var(--navbar-dropdown-background-color)
    border-bottom-left-radius: var(--navbar-dropdown-radius)
    border-bottom-right-radius: var(--navbar-dropdown-radius)
    border-top: var(--navbar-dropdown-border-top)
    box-shadow: 0 8px 8px var(--navbar-shadow-color)
    display: none
    font-size: 0.875rem
    +utilities.ltr-position(0, false)
    min-width: 100%
    position: absolute
    top: 100%
    z-index: var(--navbar-dropdown-z)
    .navbar-item
      padding: var(--navbar-item-padding)
      white-space: nowrap
    a.navbar-item
      +utilities.ltr-property("padding", 3rem)
      &:focus,
      &:hover
        background-color: var(--navbar-dropdown-item-hover-background-color)
        color: var(--navbar-dropdown-item-hover-color)
      &.is-active
        background-color: var(--navbar-dropdown-item-active-background-color) !important
        color: var(--navbar-dropdown-item-active-color)
    .navbar.is-spaced &,
    &.is-boxed
      border-radius: var(--navbar-dropdown-boxed-radius)
      border-top: none
      box-shadow: var(--navbar-dropdown-boxed-shadow)
      display: block
      opacity: 0
      pointer-events: none
      top: calc(100% + var(--navbar-dropdown-offset))
      transform: translateY(-5px)
      transition-duration: var(--speed)
      transition-property: opacity, transform
    &.is-right
      left: auto
      right: 0
  .navbar-divider
    display: block
  .navbar > .container,
  .container > .navbar
    .navbar-brand
      +utilities.ltr-property("margin", -.75rem, false)
    .navbar-menu
      +utilities.ltr-property("margin", -.75rem)
  // Fixed navbar
  .navbar
    &.is-fixed-bottom-desktop,
    &.is-fixed-top-desktop
      +navbar-fixed
    &.is-fixed-bottom-desktop
      bottom: 0
      &.has-shadow
        box-shadow: 0 -2px 3px var(--navbar-shadow-color)
    &.is-fixed-top-desktop
      top: 0
  html,
  body
    &.has-navbar-fixed-top-desktop
      padding-top: var(--navbar-height)
    &.has-navbar-fixed-bottom-desktop
      padding-bottom: var(--navbar-height)
    &.has-spaced-navbar-fixed-top
      padding-top: calc(var(--navbar-height) + calc(var(--navbar-padding-vertical) * 2))
    &.has-spaced-navbar-fixed-bottom
      padding-bottom: calc(var(--navbar-height) + calc(var(--navbar-padding-vertical) * 2))
  // Hover/Active states
  a.navbar-item,
  .navbar-link
    &.is-active
      color: var(--navbar-item-active-color)
    &.is-active:not(:focus, :hover)
      background-color: var(--navbar-item-active-background-color)
  .navbar-item.has-dropdown
    &:focus,
    &:hover,
    &.is-active
      .navbar-link
        background-color: var(--navbar-item-hover-background-color)

// Combination

.hero
  &.is-fullheight-with-navbar
    min-height: calc(100vh - var(--navbar-height))
